html, body {
    font-family: 'MyWebFont', sans-serif !important;
    font-weight: 500;
}

.flex-wrapper {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    justify-content: space-between
}

.burgundy {
    background-color: #9B3441 !important;
}

.burgundy-font-color {
    color: #9B3441;
}

.gray-font-color {
    color: #a0a0a0;
}

.navbar-custom .navbar-nav .nav-link {
    color: #ffffff !important;
}

.navbar-custom .nav-item > .nav-link.active {
    color: #FFD68E !important;
}

section {
    padding-top: 100px;
    padding-bottom: 100px;
}

.font-weight-custom-bold {
    font-weight: 700;
}

.font-weight-custom-bolder {
    font-weight: 500;
}

.line-height-custom {
    line-height: 1.2em;
}

.advantage-cards .card {
    border: 0;

}

.advantage-img {
    height: 70px;
    width: 70px;
}

.pagewide {
    height: 200px;
    z-index: 9999;
    background-color: white;
}

@media (width: 576px) {
    .main-container {
        padding-top: 0;
    }

    .navbar-custom {
        margin-top: 0;
    }
}

@media (min-width: 1px) {
    .modal.right .modal-dialog {
        width: 100%;
        top: 0;
    }
    .custom-form {
        width: 70%;
    }

    #sendBtn {
        /*height: 100px;*/
        /*position: absolute;*/
    }

    .my-2gis-map {
        height: 350px;
    }
}


@media (min-width: 577px) {
    .navbar-custom {
        margin-top: 200px;
    }

    .logo-mini {
        width: 40%;
        display: inline-block;
    }

    .logo-mini img {
        width: 100%;
    }

    .info-mini {
        height: 100%;
        float: right;
        max-width: 60%;
    }

    .main-container {
        padding-top: 170px;
    }

    .owl-prev {
        width: 50px;
        height: 50px;
        position: absolute;
        top: 20%;
        margin-left: -90px;
        display: block !important;
        border: 0 solid black;
    }

    .owl-next {
        width: 50px;
        height: 50px;
        position: absolute;
        top: 20%;
        right: -90px;
        display: block !important;
        border: 0 solid black;
    }

    .modal.right .modal-dialog {
        width: 400px;
        top: 200px;
    }

    .my-2gis-map {
        height: 500px;
    }
}

@media (min-width: 768px) {
    .logo-mini {
        display: inline-block;
        width: 40%;
    }

    .logo-mini img {
        width: 70%;
        margin-top: 3em;
        margin-left: 3em;
    }

    .info-mini {
        height: 100%;
        float: right;
        max-width: 70%;
    }

    .info-mini img {
        width: 100%;
    }

    .advantage-cards .card.border-dashed {
        border-right: 1px dashed;
    }

    .my-2gis-map {
        height: 700px;
    }
}

